
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.app-header {
  width: 100%;
  height: 50px;
  background: #26a2ff;
}
.app-header p {
  line-height: 50px;
  margin: 0 20px;
}
.app-header a {
  color: #ffffff;
  font-size: 16px;
  font-weight: bold;
  text-decoration: none;
}



